html {
  font-family: 'Courier New', Courier, monospace;
}

h1 {
  font-weight: bold;
  font-size: 40px;
  background-color:rgb(255, 205, 0);
  border-radius: 5px;
  padding: 10px;
}

h2 {
  font-weight: bold;
  font-size: 30px;
}

.pixel {
  background: white;
  float: left;
  padding: 0;
  text-align: center;
  display: table-cell;
}

.image-row {
  clear: both;
  display: table;
  float: left;
}

.warning-entry {
  color: red
}

.info-text {
  width: 300px;
}

.input-description {
  display: inline;
}

.small-text-input {
  width: 20px;
}
.vari-text-input {
  display:inline-block;
  border: 1px solid black;
  min-height: 10px;
  min-width: 600px;
  max-width: 600px;
}

.error-box{
  display: block;
}

.area{
  display: table-cell;
  /*border: 1px solid black;*/
  background-color: #F5F5F5;
  margin: 5px;
  padding: 20px;
  padding-top: 0px;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: red;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: red;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: red;
}